import React, { Component } from 'react';
import './App.css';
import superagent from 'superagent';
import injectTapEventPlugin from 'react-tap-event-plugin';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import {List, ListItem} from 'material-ui/List';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';

// needed for Dialog
injectTapEventPlugin();

class App extends Component {
  
  constructor(props){
    super(props);
    this.state = {
      open: false, // dialog state
      search: {
        location: '',
        query: ''
      },
      venues: [], // store our results
      resultState: '', // parsing state to h3 element above list
      resultCount: '',
      errorQueryResponse:'',
      errorLocationResponse:'',
    }
  }

  handleClose = () => {
    this.setState({open: false});
  };

  handleChange = (field, event) => {
    let search = Object.assign({}, this.state.search)
    search[field] = event.target.value
    this.setState({
      search: search
    })
  }

  selectLocation = (venue, event) => {
    event.preventDefault()
     this.setState({
      selectedVenue: venue,
      open: true
     })
  }

  countResults = () => {
    this.setState({
      resultCounter: this.state.resultCounter + 1
    });
  }

  callApi = () => {

    if(this.state.search.query === ''){
      this.setState({errorQueryResponse:'Query cannot be blank'});
      return;
    } else {
      this.setState({errorQueryResponse:''});
    }

    if(this.state.search.location === ''){
      this.setState({errorLocationResponse:'Location cannot be blank'});
      return;
    } else {
      this.setState({errorLocationResponse:''});
    }

    let return_venues = [];
    const url = "https://api.foursquare.com/v2/venues/search"
    const search_params = {
      v: '20170708',
      near: this.state.search.location,
      query: this.state.search.query,
      client_id: 'VMJUZG4ZRQEEGDKTBIQUXLPAVKM5GENKQAQYOQT5SKRY045G',
      client_secret: 'ALVZKW13TYSZQLYIQTHJL2GGQ011WICDXY0AXZXQNJYPHMP3',
    }

    superagent
    .get(url)
    .set('Accept', 'application/json')
    .query(search_params)
    .end((err, data) => {
      if (err) {
        this.setState({
          resultState: 'No results'
        })
        return;
      }

      for(let x in data.body.response.venues){
        if(data.body.response.venues[x].location.city){
          return_venues.push(data.body.response.venues[x])
        }
      }

      this.setState({
        venues: return_venues,
        resultState: 'Results',
        resultCount: return_venues.length
      })

    })
  }

  render() {

    const actions = [
      <FlatButton
        label="Open in Maps"
        primary={true}
        href={(this.state.selectedVenue) ? 'https://www.google.com/maps/search/?api=1&query='+this.state.selectedVenue.location.lat + ',' + this.state.selectedVenue.location.lng : ''}
      />,
      <FlatButton
        label="Close"
        primary={true}
        onTouchTap={this.handleClose}
      />,
    ];

    // Helper to check if specific field in JSON from 4S exists to avoid "undefined"
    const checkDataExists = (data) => {
      if (data) {
        return data;
      } else {
        return "";
      }
    }

    return (
      <MuiThemeProvider>
      <div className="App">
        
        <div className="App-header">
          <h1>Foursqaure Search</h1>
          <h2>
            {(this.state.search.query || this.state.search.location) ? 'Searching for ' : ''} 
            {' ' + this.state.search.query}
            {(this.state.search.query && this.state.search.location) ? ' in ' : ''} 
            {this.state.search.location}
          </h2>
        </div>
       
        <TextField 
          hintText="What are you looking for?" 
          value={this.state.search.query} 
          onChange={this.handleChange.bind(this,'query')}
          errorText={this.state.errorQueryResponse}
        />
        <br /> 
        <TextField 
          hintText="Where do you want it?" 
          value={this.state.search.location} 
          onChange={this.handleChange.bind(this,'location')}
          errorText={this.state.errorLocationResponse}
        />
        <br />

        <RaisedButton label="Go!" onClick={this.callApi}/>

        <br/>

        <Dialog
            title={(this.state.selectedVenue) ? this.state.selectedVenue.name : ''}
            actions={actions}
            modal={false}
            open={this.state.open}
            onRequestClose={this.handleClose}
          >
          <p>{(this.state.selectedVenue) ? checkDataExists(this.state.selectedVenue.location.address) : ''}</p>
          <p>
          {
            (this.state.selectedVenue) 
            ? this.state.selectedVenue.location.city 
            + ', ' 
            + checkDataExists(this.state.selectedVenue.location.state)
            : ''
          }  
          </p>
          <p>
            {
              (this.state.selectedVenue) 
              ? checkDataExists(this.state.selectedVenue.location.country) 
              + ' ' 
              + checkDataExists(this.state.selectedVenue.location.postalCode)
              : ''
            }

          </p>

        </Dialog>

        <h3>{this.state.resultState} 
        {
          (this.state.resultState)
          ? ' (' + this.state.resultCount + ')'
          : ''
        } </h3>

        <div>
          <List>
            { this.state.venues.map((venue, i) => {
              
              if (venue.location.city) {
                return (
                  <ListItem key={venue.id} onClick={this.selectLocation.bind(this, venue)} primaryText={venue.name + ' (' + venue.location.city + ')'} />
                  )
              }
              return false;
              }) 
            }
          </List>
        </div>
      </div>
      </MuiThemeProvider>
    );
  }
}

export default App;


